<template>
  <!-- import { close } from 'fs'; -->
  <v-app>
    <v-content>
      <!-- 顶部logo -->
      <v-toolbar-title class="logo">
        <img src="../assets/unicom-logo.png" alt="" />
        <span>多用户远程控制系统</span>
      </v-toolbar-title>
      <!-- 中间内容部分 -->
      <v-container fluid fill-height class="content">
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md3>
            <!-- 表格内容 -->
            <v-flex xs12 sm12 md12>
              <v-card class="elevation-12 mlog">
                <v-container fluid grid-list-md class="pb-0">
                  <v-layout row align-center justify-center fill-height>
                    <v-flex d-flex xs12 sm12 md12 class="login">
                      <div class="userlogin">
                        <div class="" style="margin-top:20px;height:30px">
                          <span>登录</span>
                        </div>
                        <br />
                        <span>多用户远程控制系统</span>
                        <br />
                        <em class="normals text-capitalize"
                          >Multi-user remote control system</em
                        >
                        <br />
                      </div>
                    </v-flex>
                  </v-layout>
                </v-container>
                <v-card-text>
                  <v-form ref="loginform" :model="loginObj" lazy-validation>
                    <v-container>
                      <v-layout align-center justify-center>
                        <v-flex xs12 md12>
                          <v-text-field
                            prepend-icon="fa-user"
                            color="green"
                            name="user"
                            label="用户名"
                            type="text"
                            v-model="loginObj.username"
                            required
                            outline
                            >kaikai</v-text-field
                          >
                        </v-flex>
                      </v-layout>
                      <v-layout align-center justify-center>
                        <v-flex xs12 md12>
                          <v-text-field
                            prepend-icon="fa-lock"
                            color="green"
                            name="pwd"
                            label="密码"
                            v-model="loginObj.pwd"
                            type="password"
                            required
                            outline
                          ></v-text-field>
                        </v-flex>
                      </v-layout>
                      <v-layout align-center justify-center>
                        <v-flex xs12 md12>
                          <!-- block是可以让button占满外边盒子的宽度 -->
                          <v-dialog v-model="dialog" width="290">
                            <template v-slot:activator="{ on }">
                              <v-btn
                                block
                                color="#f1870d"
                                width="400"
                                v-on="on"
                                @click.prevent="handleLogin()"
                                >登录</v-btn
                              >
                            </template>
                            <v-card width="290">
                              <v-card-title
                                class="headline grey lighten-2"
                                primary-title
                                >信息</v-card-title
                              >
                              <v-card-text>{{ msgresp }}</v-card-text>
                              <v-card-actions class="diainfo">
                                <v-btn
                                  color="info"
                                  style="float:right"
                                  @click="dialog = false"
                                  >确定</v-btn
                                >
                              </v-card-actions>
                            </v-card>
                          </v-dialog>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-form>
                </v-card-text>
              </v-card>
            </v-flex>
            <!-- © 版权信息 -->
            <v-flex
              class="copyright"
              lighten-2
              py-2
              text-xs-center
              white--text
              xs12
            >
              &copy; 智邮开源通信研究院 {{ new Date().getFullYear() }}
            </v-flex>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>
<style scoped>
.mlog {
  border-radius: 12px;
}
.content {
  position: absolute;
  height: 740px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../assets/images/background.png) #f1870d repeat-x;
}
.logo img {
  border-width: 0px;
  position: absolute;
  left: 0px;
  top: 0px;
  /*width: 102px;*/
  height: 90px;
}
.logo span {
  border-width: 0px;
  position: absolute;
  left: 360px;
  top: 27px;
  width: 397px;
  height: 36px;
  font-family: '微软雅黑 Bold', '微软雅黑 Regular', '微软雅黑';
  font-weight: 700;
  font-style: normal;
  font-size: 36px;
  color: #f1870d;
  text-align: center;
  line-height: 36px;
}

.login .userlogin {
  text-align: center;
  font-family: '微软雅黑';
  font-weight: 400;
  font-style: normal;
  font-size: 28px;
  color: #f1870d;
  line-height: 36px;
}
.copyright {
  color: white;
  bottom: 0;
}
.normals {
  font-size: 12px;
}
.diainfo {
  margin-left: 162px;
}
</style>
<script>
// 第三种

export default {
  name: 'LoginPage',
  data() {
    return {
      dialog: false,
      msgresp: '',
      loginObj: {
        username: 'witcomm',
        pwd: '123456'
      }
    }
  },
  metaInfo() {
    return {
      title: '多用户远程控制系统'
    }
  },

  methods: {
    handleLogin() {
      localStorage.removeItem('token')
      this.$axios({
        method: 'post',
        url: '/login/',
        data: this.loginObj,
        transformRequest: [
          function(data) {
            let ret = ''
            for (let it in data) {
              ret +=
                encodeURIComponent(it) +
                '=' +
                encodeURIComponent(data[it]) +
                '&'
            }
            return ret
          }
        ],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data.code == 200) {
          localStorage.setItem('token', res.data.token)
          this.msgresp = '登录成功'
          this.$router.push({ name: 'Dashboard' })
        } else {
          this.msgresp = res.data.msg
        }
      })
    }
  }
}
</script>
